@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700);
html, body {
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
}

hr {
    border-color: #3EABB8;
}

h1, h2, h3 {
    color: #3EABB8;
}

h3 {
    padding-top: 1rem;
}

.a16-9 {
    position: relative;
    padding-bottom: 56.25%;
}

.a1-1 {
    position: relative;
    padding-bottom: 100%;
}

.inside-aspect-ratio {
    position: absolute;
    overflow: hidden;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}

#about-content {
    overflow: auto;
}

#svg {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 50%;
}

.navbar {
    -webkit-transition: background-color .2s linear;
    transition: background-color .2s linear;
}

.navbar-dark .navbar-nav .nav-link {
    font-size: 1.1rem;
    color: #efefef;
}

.navbar-expand-md .navbar-collapse {
    text-align: center;
}

.progress-component:hover {
    background-color: rgb(68, 68, 68);
}

.progress-component {
    padding: .5rem 0;
    border-radius: 5px;
    cursor: default;
}

.progress {
    background-color: #008E9E;
    height: 1.5rem;
}

.progress-bar {
    background-color: #3EABB8;
}

.card {
    background: none;
    border: none;
    cursor: pointer;
    height: 100%;
}

.card-img-top {
    min-width: 100%;
    min-height: 100%;
}

.modal-content,
#footerLinks a:hover,
.card:hover {
    background-color: #343434;
}

#btn-cv {
    margin: 1.5rem auto 0 auto;
}

#btn-cv a {
    color: inherit;
    text-decoration: none;
    font-size: 1.1rem;
    text-transform: capitalize;
    padding: .5rem 1rem;
}

.btn-primary {
    background-color: #3EABB8;
    border: none;
}

.btn-primary:hover {
    background-color: #008E9E;
}

.btn-primary:focus,
.btn-primary:active {
    background-color: #3EABB8 !important;
}

.modal-header,
.modal-footer {
    border-color: #666;
}

.modal-body,
.modal-content .close {
    color: white;
}

.modal-title {
    color: rgb(62, 171, 184);
}

.carousel-indicators {
    margin-bottom: 0;
}

.contact {
    font-size: 1.5rem;
    color: inherit;
    font-weight: 700;
}

#footerLinks a, 
#footerLinks svg,
.contact a,
.contact span {
    color: rgb(62, 171, 184);
}

.panel {
    color: white;
    padding: 4rem 0;
}

.footer {
    padding: 1.5rem 0;
}

#footerLinks a {
    display:inline-block;
    width:100%;
    height:100%;
    padding: .5rem 0;
    border-radius: 5px;
}

#footerLinks a:hover {
    text-decoration: none;
}

#footerLinks a:hover span {
    text-decoration: underline;
}

/* scrollbar styling */

body::-webkit-scrollbar {
    width: 1em;
}
 
body::-webkit-scrollbar-track {
    background-color: #ccc;
}
 
body::-webkit-scrollbar-thumb {
  background-color: #34A2AE;
  outline: 1px solid #008E9E;
}
